import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from '../HeaderComponent/header';

import './onama.css';


class Onama extends Component {
  render() {
    return (
    	<div className="informacije">
				<div className="fixano">
					<Header />
				</div>
				<div className="main">
					<div id="opac">\
						<h3> Jo od 1987. godine pogrebne usluge Martinovi su tu da Vam prue podrku u najbolnijim trenucima.</h3>
						<h4>Uvaavajui Vae zahtjeve i potrebe formiramo uslugu kojom moete zavriti kompletnu organizaciju sahrane vaih najmilijih. Nai deurni brojevi telefona su vam dostupni 24 sata na dan.</h4>
					</div>
				</div>
			</div>
    );
  }
}

export default Onama;


